/* Quick Action */
.quickAction1 {
    /* height: 60%; */
    display: none;
    height:auto;
    min-height:60px;
    height:auto !important;        
    height:30px;
    width: 50px;
    position: fixed;
    z-index: 3;
    bottom: 0%;
    right: 0;
    background-color:#F5F5F5;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 0;
    padding-bottom: 0;
    color: white;
}
      
.quickAction1 a {
    text-align: left;
    text-decoration: none;
    font-size: 10px;
    text-decoration-color: green;
    display: block;
    transition: 0.3s;
    color: black;
    font-weight: bold;
}
      
.quickAction1 a:hover {
    color: #00D26B;
}
      
.quickAction1 .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}

.quickIcon {
    width: 50px;
    padding: 10px;  
}

/* quick action button */
.quickActionButton {
    text-align: center;
    height: 50px;
    width: 50px;
    position: fixed;
    z-index: 3;
    bottom: 125px;
    right: 23px;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 0;
    padding-bottom: 0;
    background-color: #2659B6 !important;
    border: none !important;
    border-radius: 0%;
    padding: 8px;
}

.icon-position {
    margin-top: 5px;
}

